import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Box from '@material-ui/core/Box';

import particlesType from 'components/__types__/particles';
import ParticlesFieldTable from 'components/particles-field-table/ParticlesFieldTable';

const ParticlesDetails = ({ t, particles }) => {
  return (
    <Box>
      <h3 data-testid="details_title">
        {t('common.widgetName', {
          widgetNamePlaceholder: 'Particles',
        })}
      </h3>
      <ParticlesFieldTable particles={particles} />
    </Box>
  );
};

ParticlesDetails.propTypes = {
  particles: particlesType,
  t: PropTypes.func.isRequired,
};

ParticlesDetails.defaultProps = {
  particles: {},
};

export default withTranslation()(ParticlesDetails);
